    cursor: none;
  }
   */
.cursor-dot,
.cursor-dot-outline {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out,
    background-color 0.3s ease-in-out, border 0.3s ease-in-out;
  z-index: 99;
}

.cursor-dot {
  width: 0px;
  height: 0px;
  border-top: 11px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 13px solid #333;
  display: none;
}

#cursor-dot::before {
  content: "";
  width: 0px;
  height: 0px;
  border-top: 12px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 14px solid #f6c04b;
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-90%, -80%);
}

.cursor-dot-outline {
  position: absolute;
  width: 5.5vw;
  height: 5.5vw;
  border-radius: 50%;
  background-color: #333;
  /* mix-blend-mode:difference; */
  transform: translate(-50%, -50%) scale(0.1);
  justify-content: center;
  align-items: center;
  color: #f9f9f9;
  opacity: 0;
}

.cursor-eye,
.cursor-back,
.cursor-more,
.cursor-next {
  position: absolute;
  width: 5.5vw;
  height: 5.5vw;
  top: 0;
  left: 0;
  opacity: 1;
}
.cursor-eye {
  background: #f6c04b url(./assets/cursor-eye.svg) no-repeat 50% / 45%;
}
.cursor-back {
  background: #a9c368 url(./assets/cursor-back.svg) no-repeat 50% / 43%;
}
.cursor-more {
  background: #a9c368 url(./assets/cursor-more.svg) no-repeat 50% / 43%;
}
.cursor-prev {
  background: #f6c04b url(./assets/cursor-prev.svg) no-repeat 50% / 43%;
}
.cursor-next {
  background: #f6c04b url(./assets/cursor-next.svg) no-repeat 50% / 43%;
}

.cursor-zoom{
  background-color:transparent;
  border: solid .2vw #f6c04b;
}

