.footer {
  position: relative;
  width: 100%;
  padding: 0 21%;
  height: 20vh;
  min-height: 150px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #f9f9f9;
  user-select: none;
}

.footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 30%;
  height: 0.13vw;
  min-height: 5px;
  background: #333;
  border-radius: 50px;
}

.footer p {
    padding: 0;
    font-size: 16px;
    line-height: 16px;
    color: #333;
}

.footer .logo {
    z-index: 1;
    pointer-events: none;
    width: 2vw;
    height: 2vw;
    min-width: 40px;
    min-height: 40px;
}

.pgs-cprght {
  display: flex;
  align-items: center;
  flex-direction: row;
}

@media screen and (max-width: 1080px) {
  .footer {
    padding: 0 11.5%;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 0 10%;
  }
  .footer::before {
    width: 30%;
  }
}
@media screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    justify-content: center;
    align-items: space-between;
    padding: 0 5%;
  }
}
