.theme-app {
  width: 1.8vw;
  height: 1.8vw;
  min-width: 35px;
  min-height: 35px;
  background-repeat: no-repeat;
  background-position: 50%;
  position: absolute;
  top: 3.5vw;
  left: 50%;
  transform: translateX(-50%);
  pointer-events: painted;
  cursor: pointer;
  background-size: 90%;
  z-index: 5;
  transition: 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: none;
}

