.navigation {
  position: fixed;
  width: 100%;
  height: auto;
  /* background: rgba(255, 255, 255, 0.2); */
  z-index: 98;
  display: flex;
  align-items: center;
  padding: 3% 12%;
  justify-content: space-between;
  pointer-events: none;
  top: 0;
  left: 0;
}

.navigation .active,
.navigation .active .logo {
  pointer-events: none;
}

.logo {
  width: 2.5vw;
  height: 2.5vw;
  min-width: 45px;
  min-height: 45px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  pointer-events: painted;
  cursor: none;
}

.logo::before,
.logo::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: transform .7s cubic-bezier(.68, -.55, .27, 1.7), background-color .4s ease;
}

.logo::before {
  background-color: #333;
}

.logo:hover::before,
.logo.act::before {
  background-color: #da554b;
  transform: scale(1.3);
}

.logo svg {
  position: absolute;
  width: 90%;
  height: 90%;
  fill: #f7f5e9;
  transition: transform .7s cubic-bezier(.68, -.55, .27, 1.7) .2s;
}

.logo:hover svg,
.logo.act svg {
  transform: rotate(-45deg);
}

.nm-btn {
  position: relative;
  width: 2.7vw;
  height: 2.7vw;
  min-width: 45px;
  min-height: 45px;
  z-index: 3;
  border-radius: 50%;
  cursor: pointer;
  pointer-events: painted;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nm-btn::before,
.nm-btn::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #333;
  border-radius: 50%;
  transition: .7s cubic-bezier(.68, -.55, .27, 1.7);
}

.nm-btn::before {
  background: #333;
}

.nm-btn::after {
  background: #f7f5e9;
  z-index: -1;
  transform: scale(1.2);
}

.nm-btn:hover::before,
.nm-btn.act::before {
  transform: scale(1.3);
}

.nm-btn:hover::after {
  transform: scale(1);
}

.nm-btn svg {
  fill: #f7f5e9;
  position: absolute;
  width: 50%;
  height: 50%;
}

.nm-btn path:nth-child(2),
.nm-btn path:nth-child(3) {
  transform: translate(0, 0) rotate(0deg);
  transform-origin: center;
  transition: 0.5s cubic-bezier(0.7, 0, 0.3, 1);
  opacity: 1;
}

.nm-btn.no-act path:nth-child(1),
.nm-btn.no-act path:nth-child(2),
.nm-btn.no-act path:nth-child(3) {
  transition: none;
}

.nm-btn path:nth-child(1) {
  transition: transform 0.5s cubic-bezier(0.7, 0, 0.3, 1), opacity 0.3s linear .1s;
}

.nm-btn.act path:nth-child(1) {
  transform: translateY(25%);
  opacity: 0;
  transition: transform 0.5s cubic-bezier(0.7, 0, 0.3, 1), opacity 0.2s linear .3s;
}

.nm-btn.act path:nth-child(2) {
  /* transform: translateX(50%); */
  transform: rotate(-45deg) translate(0%, -26%);
  transition: transform 0.4s linear 0.2s;
}

.nm-btn.act path:nth-child(3) {
  transform: rotate(45deg);
  opacity: 1;
  transition: transform 0.6s cubic-bezier(0.7, 0, 0.3, 1) 0.2s,
    opacity 0.2s linear 0s;
}


.nav-menu {
  display: flex;
  flex-direction: row;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #f7f5e9;
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  transition: 0.5s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.act {
  visibility: visible;
  pointer-events: painted;
  opacity: 1;
  transition: 0.8s cubic-bezier(0.7, 0, 0.3, 1);
}

.nav-menu.no-act {
  transition: none;
}

.nav-menu-l,
.nav-menu-c {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-menu-l {
  font-family: "Bebas Neue", cursive;
}

.nav-menu-c {
  overflow: hidden;
  color: #f9f9f9;
}

.nav-menu-c::before {
  content: "";
  width: 5px;
  height: 30%;
  position: absolute;
  background: #333;
  left: 0;
  border-radius: 50px;
}

.nav-menu-c-b {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  background-color: #da554b;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav-menu-c-a {
  position: relative;
  top: 0;
  left: 0;
  transform: translateX(30%);
  opacity: 0;
  transition: 0.1s cubic-bezier(0.7, 0, 0.3, 1) .4s;
}

.nav-menu-c-a h4 {
  color: #da554b;
}

.nav-menu-c-a div p {
  user-select: text;
  color: #333;
}

.nav-menu-c-a div a {
  color: #333;
  text-decoration: none;
}

.act .nav-menu-c .nav-menu-c-a {
  transform: translateX(0);
  opacity: 1;
  transition: 1.2s cubic-bezier(0.7, 0, 0.3, 1) .2s;
}

.nav-menu-l ul li:nth-child(1),
.nav-menu-l ul li:nth-child(2),
.nav-menu-l ul li:nth-child(3),
.nav-menu-l ul li:nth-child(4) {
  transform: translateX(-50%);
  opacity: 0;
  transition: 0.1s cubic-bezier(0, 0, 0.2, 1) .4s;
}

.act .nav-menu-l ul li:nth-child(1),
.act .nav-menu-l ul li:nth-child(2),
.act .nav-menu-l ul li:nth-child(3),
.act .nav-menu-l ul li:nth-child(4) {
  opacity: 1;
  transform: translateX(0);
  transition: 0.4s cubic-bezier(0, 0, 0.2, 1);
}

.act .nav-menu-l ul li:nth-child(1) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.8s;
}

.act .nav-menu-l ul li:nth-child(2) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.7s;
}

.act .nav-menu-l ul li:nth-child(3) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.6s;
}

.act .nav-menu-l ul li:nth-child(4) {
  transition: 0.7s cubic-bezier(0, 0, 0.2, 1) 0.5s;
}

.nav-menu-l ul li {
  margin: .8vh 0;
  list-style-type: none;
  text-align: right;
}

.nav-menu-l ul li a{
  display: block;
  color: #333;
  text-decoration: none;
  font-size: 5.4vw;
  line-height: 6.6vw;
  position: relative;
  transition: 0.5s ease;
  cursor: pointer;
  z-index: 2;
}

.nav-menu-l ul li a h2 {
  letter-spacing: -0.2vw;
  transition: 0.5s ease;
}

.nav-menu-l ul li a.active h2{
  /* color: #f6c04b; */
  color: #da554b;
}

.nav-menu-l ul li a span {}

.nav-menu-l ul li a span:nth-child(1),
.nav-menu-l ul li a span:nth-child(2),
.nav-menu-l ul li a span:nth-child(3),
.nav-menu-l ul li a span:nth-child(4),
.nav-menu-l ul li a span:nth-child(5),
.nav-menu-l ul li a span:nth-child(6),
.nav-menu-l ul li a span:nth-child(7) {
  position: relative;
  left: 0%;
}

.nav-menu-l ul li a:hover span:nth-child(1) {
  animation: 0.4s navmenul ease;
}

.nav-menu-l ul li a:hover span:nth-child(2) {
  animation: 0.4s navmenul ease 0.1s;
}

.nav-menu-l ul li a:hover span:nth-child(3) {
  animation: 0.4s navmenul ease 0.2s;
}

.nav-menu-l ul li a:hover span:nth-child(4) {
  animation: 0.4s navmenul ease 0.3s;
}

.nav-menu-l ul li a:hover span:nth-child(5) {
  animation: 0.4s navmenul ease 0.4s;
}

.nav-menu-l ul li a:hover span:nth-child(6) {
  animation: 0.4s navmenul ease 0.5s;
}

.nav-menu-l ul li a:hover span:nth-child(7) {
  animation: 0.4s navmenul ease 0.6s;
}

@keyframes navmenul {
  0% {
    left: 0%;
    color: #333;
  }

  25% {
    left: 10%;
    color: #f6c04b;
  }

  100% {
    left: 0%;
    color: #333;
  }
}

@keyframes navmenul2 {
  0% {
    left: 0%;
    color: #f7f5e9;
  }

  25% {
    left: 8%;
    color: #f6c04b;
  }

  100% {
    left: 0%;
    color: #f7f5e9;
  }
}

.nav-menu-l ul li img {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  z-index: 1;
}

.nav-menu-l ul li.act img {
  display: block;
}

.soc-icon-box {
  position: relative;
  display: flex;
  flex-direction: row;
}

.soc-icons {
  width: 2.2vw;
  height: 2.2vw;
  min-width: 45px;
  min-height: 45px;
  border-radius: 50%;
  margin-right: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: 0.4s ease;
}

.soc-icons svg {
  position: absolute;
  width: 50%;
  height: 50%;
  fill: #f7f5e9
}

.soc-icons::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #333;
  border-radius: 50%;
  transition: 0.4s ease;
  z-index: -1;
}

.soc-icons:hover::before {
  background: #f6c04b;
  transform: scale(1.2);
}

.soc-icons.email-icon {
  display: none;
}

.c-right {
  position: absolute;
  padding: 20px 0;
  bottom: 0;
  color: #333;
  width: 100%;
  text-align: center;
  z-index: 2;
}

.c-right p {
  font-size: 16px;
  line-height: 16px;
}

@media screen and (max-width: 1024px) {
  .theme-app {
    top: 50px;
  }

  .navigation {
    padding: 5% 10%;
  }
  

  body.act {
    overflow: hidden;
  }

  .nm-btn:hover::after,  .logo:hover::after {
    transform: scale(1.2);
  }
  .logo:hover::before {
    background: #333;
  }
  .logo:hover svg {
    transform: none;
  }

  .nm-btn:hover::before, .logo:hover::before {
    transform: scale(1);
  }

  .nm-btn.act::before {
    transform: scale(1.3);
  }

  .logo:hover .log-1,
  .logo:hover .log-2,
  .logo:hover .log-4,
  .logo:hover .log-5 {
    transition: none;
    transform: translate(0, 0);
  }

  .nav-menu-l ul li a:hover span:nth-child(1),
  .nav-menu-l ul li a:hover span:nth-child(2),
  .nav-menu-l ul li a:hover span:nth-child(3),
  .nav-menu-l ul li a:hover span:nth-child(4),
  .nav-menu-l ul li a:hover span:nth-child(5),
  .nav-menu-l ul li a:hover span:nth-child(6),
  .nav-menu-l ul li a:hover span:nth-child(7) {
    animation: none;
  }
}

@media screen and (max-width: 768px) {
  .theme-app {
    top: 30px;
  }

  h2 {
    text-align: center;
  }

  .navigation {
    padding: 40px 50px;
  }

  .nav-menu {
    flex-direction: column;
  }

  .nav-menu-c {
    height: 30%;
  }

  .nav-menu-l {
    padding-top: 20%;
  }

  .nav-menu-l ul li a h2{
    font-size: calc(45px + 4vw);
    line-height: calc(32px + 3.5vw);
    text-align: center;
  }

  .nav-menu-l ul {
    transform: translateX(0);
    transition: none;
  }

  .act .nav-menu-l ul {
    transform: translateX(0);
    transition: none;
  }


  .nav-menu-l ul li:nth-child(1),
  .nav-menu-l ul li:nth-child(2),
  .nav-menu-l ul li:nth-child(3) {
    transform: translateY(-10vh);
    opacity: 0;
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.5s;
  }

  .act .nav-menu-l ul li:nth-child(1),
  .act .nav-menu-l ul li:nth-child(2),
  .act .nav-menu-l ul li:nth-child(3) {
    opacity: 1;
    transform: translateY(0);
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.2s;
  }

  .act .nav-menu-l ul li:nth-child(1) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.5s;
  }

  .act .nav-menu-l ul li:nth-child(2) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.4s;
  }

  .act .nav-menu-l ul li:nth-child(3) {
    transition: 0.5s cubic-bezier(0, 0, 0.2, 1) 0.3s;
  }

  .nav-menu-c-a {
    text-align: center;
  }

  .nav-menu-c-b,
  .nav-menu-c-a div div:nth-child(1),
  .nav-menu-c-a div div:nth-child(2) {
    display: none;
  }

  .nav-menu-c-a div div:nth-child(3) div {
    display: flex;
  }

  .soc-icons {
    margin: 0 7px;
  }

  .soc-icons.email-icon {
    display: block;
  }

  .nav-menu-c .nav-menu-c-a {
    transform: translateY(4vh);
    transition: 0.1s linear 0.4s;
  }

  .act .nav-menu-c .nav-menu-c-a {
    transform: translateY(0);
    transition: 1s cubic-bezier(0.7, 0, 0.3, 1);
  }

  .nav-menu-c {
    justify-content: center;
    align-items: flex-start;
  }

  .nav-menu-c::before {
    width: 40%;
    height: 2px;
    left: auto;
    display: none;
  }

  .nm-btn, .logo  {
    min-width: 40px;
    min-height: 40px;
  }
}

@media screen and (max-width: 500px) {

  .navigation {
    padding: 30px 30px;
  }

  .logo, .c-right {
    display: none;
  }

  .theme-app {
    left: 12%
  }

  .nav-menu-l ul li a h2{
    font-size: calc(45px + 4vw);
    line-height: calc(32px + 3.5vw);
    text-align: center;
  }

  .nav-menu-c {
    height: 40%;
}

  .soc-icon-box{
    justify-content: center;
    flex-wrap: wrap;
  }

  .soc-icons {
    margin: 5px 7px;
}

}