.panel {
  height: 100%;
  min-height: 100vh;
}

.panel h1 {
  font-size: 1.8em;
  font-weight: 300;
  margin: 0 auto;
}

.panel p,
.panel li {
  text-align: left;
  margin: 0.3em 0 1em 0;
}

.panel p code,
.panel li code {
  background-color: rgba(255, 255, 255, 0.15);
  padding: 2px 4px;
  border-radius: 5px;
}

.panel.description {
  padding-bottom: 60px;
}

.panel {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
  top: 0;
  left: 0;
}

.blue {
  flex-direction: row;
  user-select: none;
  pointer-events: none;
}

.red {
  height: 70vh;
  min-height: 700px;
  background: #f9f9f9;
}

.orange img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.purple {
  background-color: #c4bb6b;
}

/* .gray {
  background-color: #777;
} */

.line {
  width: 100%;
  max-width: 1400px;
  height: 20px;
  margin-top: 20px;
  position: relative;
  z-index: 7;
  display: inline-block;
  background-color: rgba(255, 255, 255, 1);
}

.wel-h2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 12%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  z-index: 1;
  overflow: hidden;
}

.quotes {
  position: absolute;
  width: 50%;
}

.quotes h2 {
  position: relative;
  font-size: calc(23px + 3.6vw);
  line-height: calc(18px + 2.9vw);
  letter-spacing: -0.1vw;
  color: #da554b;
  font-family: "FuturaIWS";
  cursor: default;
  text-align: left;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: 0;
  left: 30%;
}

.quotes h2 span {
  color: #333;
}

.quotes p {
  font-family: "FuturaIWS";
  font-size: 1.3vw;
  line-height: 0;
  font-weight: lighter;
  margin-top: 1.5vw;
  opacity: 0;
  transform: translateX(20%);
}

.tr--1 {
  position: absolute;
  width:50%;
  height: 100%;
  right: 0;
  z-index: 1;
  bottom: -100%
}

.tr--1 svg {
  position: absolute;
  width: 100%;
  height: auto;
  right: 0;
}

.tr--1 svg{bottom: -23%;}

.st0, .st01, .st02 {fill:#D9544B;}
.st01{fill:#D9544B}
.st02{fill:#D9544B;}
.st1{fill:#f6c04b; opacity: 0.98;}
.st2{fill:#26A397;}
.st3, .st31{fill:#A9C368; opacity: 0.98;}
.st4{fill:#333333;opacity: 0.98;}
.st5, .st51{fill:#4E9CB0;opacity: 0.98;}

.act-h .st0, .act-h .st01, .act-h .st02{animation:8s animTr1 infinite ease .6s;}
.act-h .st01{animation:8s animTr1 infinite ease .97s;}
.act-h .st02{animation:8s animTr1 infinite ease 1.3s;}
.act-h .st1{ animation:8s animTr1 infinite ease; }
.act-h .st2{animation:8s animTr2 infinite ease .2s;}
.act-h .st3, .act-h .st31{animation:8s animTr2 infinite ease 1s;}
.act-h .st31{animation:8s animTr2 infinite ease 1.5s;}
.act-h .st4{ animation:7s animTr1 infinite ease 2s;}
.act-h .st5, .act-h .st51{animation:7s animTr1 infinite ease .5s;}
.act-h .st51{animation:7s animTr1 infinite ease .77s;}

@keyframes animTr1 {
  0% {transform: translate3d(0,0,0);}
  50% {transform: translate3d(0,15%,0);}
  100% {transform: translate3d(0,0,0);}
}
@keyframes animTr2 {
  0% {transform: translate3d(0,0,0);}
  50% {transform: translate3d(0,10%,0);}
  100% {transform: translate3d(0,0,0);}
} 

.scroll-down {
  position: absolute;
  bottom: 6vh;
  left: 12%;
  width: 2.4vw;
  height: 2.4vw;
  min-width: 50px;
  min-height: 50px;
  transform: translate(0, 0);
  text-transform: uppercase;
  font-size: 14px;
  overflow: visible;
  z-index: 7;
  opacity: 1;
  transition: none;
}

.scroll-down svg {
  fill: #333;
}

.scroll-down svg .sr-d {
  fill: #f7f5e9;
  animation: 1.5s scrollDown infinite ease;
}

@keyframes scrollDown {
  0% {
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    transform: translateY(20%);
    opacity: 0;
  }
}

.pre-anim.before-act .scroll-down {
  opacity: 0;
}

.pre-anim.act .scroll-down {
  opacity: 1;
  transition: 1s ease-out 0.2s;
}

