.page-nav {
  position: absolute;
  right: 12.5%;
  width: auto;
  height: 100vh;
  padding: 2vh 0;
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.page-nav-a {
  display: flex;
  flex-direction: column;
}

.page-nav-a a {
  width: 1.1vw;
  height: 1.1vw;
  min-width: 30px;
  min-height: 30px;
  display: flex;
  margin: 5px 0;
  font-size: 0%;
  position: relative;
  justify-content: center;
  align-items: center;
}

.page-nav-a a::after {
  content: "";
  position: absolute;
  width: 50%;
  height: 50%;
  min-height: 5px;
  transition: 0.5s ease-out;
  border-radius: 50%;
  transform: scale(0.6);
  background: #222;
}

.page-nav-a a::before {
  content: "";
  position: absolute;
  width: 90%;
  height: 90%;
  min-height: 5px;
  transition: 0.5s ease-out;
  border-radius: 50%;
  transform: scale(0);
  background: #f7f5e9;
  transition: .5s cubic-bezier(.68, -.55, .27, 1.7);
}

.page-nav-a a.active,
.page-nav-a a.active::after,
.page-nav-a a.active::before,
.page-nav-a a:hover::after,
.page-nav-a a:hover::before {
  transform: scale(1);
}

.page-nav-a a.active::after {
  background: #f6c04b;
}


#sectionPin {
  height: 100vh;
  overflow: hidden;
  display: flex;
  left: 0;
  position: relative;
}

.pin-wrap {
  height: 100vh;
  /* min-width: 280vw; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 2vw;
  user-select: none;
}

.pin-wrap h2 {
  /* min-width: 60vw; */
  padding: 0 1vw;
  color: #f7f5e9;
  transition: 0.5s ease;
}
.pin-wrap h3 {
  color: #f7f5e9;
}

.pin-wrap a {
  text-decoration: none;
}

.pin-wrap a:hover h2 {
  opacity: 0;
  transform: scale(0.95);
}

.pin-wrap .img-hor {
  width: 38vw;
  min-width: 900px;
  height: 65vh;
  position: relative;
  overflow: hidden;
  margin: 0 3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3vw;
  cursor: none;
}

.pin-wrap .img-hor .img-cont {
  transform: scale(1.16);
  transition: 0.8s ease-in-out;
  cursor: none;
}

.pin-wrap .img-hor a:hover .img-cont {
  transform: scale(1.01);
}

.skewElemPrlx {
  position: absolute;
  width: 70%;
  right: 1vw;
  bottom: -2vw;
  z-index: 2;
  color: #f9f9f9;
  pointer-events: none;
}

.img-cont-anim {
  width: 100%;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  margin-top: -50vh;
  z-index: 5;
}

@media (max-width:1366px){
  .tr--1{
    width: 60%;
}

.tr--1 svg{bottom: -25%;}

}

@media screen and (max-width: 1024px) {

  .tr--1 {
    width: 55%;
  }
  .tr--1 svg{bottom: -10%;}

  .wel-h2 p {
    font-size: 20px;
    line-height: 20px;
  }

  .blue {
    flex-direction: column;
  }

  .page-nav {
    display: none;
  }

  #sectionPin {
    height: auto;
    display: flex;
    left: 0;
    position: relative;
  }

  .pin-wrap {
    height: auto;
    width: 100%;
    min-width: 100px;
    flex-direction: column;
    align-items: unset;
    position: relative;
    padding: 0;
  }

  .pin-wrap h2 {
    margin: 60px 10%;
  }

  .pin-wrap .img-hor {
    width: 100%;
    min-width: 100px;
    height: 50vh;
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 0;
    left: 0;
  }

  .skewElemPrlx {
    position: relative;
    width: 100%;
    right: auto;
    bottom: auto;
    color: #f9f9f9;
    pointer-events: none;
    text-align: center;
    margin: 20px 0 30px 0;
    padding: 0 10%;
  }

  .scroll-down {
    bottom: 8vh;
  }
}

@media screen and (max-width: 768px) {


  .tr--1 {
    width: 80%;
  }
  .tr--1 svg{bottom: -12%;}

  .wel-h2 {
    padding: 0 10%;
  }

  .quotes {
    width: 100%;
    left: 0;
    padding: 0 10%;
  }

  .quotes h2 {
    white-space: normal;
  }

  .blue {
    flex-direction: column;
  }

  .scroll-down {
    bottom: 8vh;
    left: 12%;
  }

  .pre-anim.before-act .quotes h2 {
    letter-spacing: -0.1vw;
  }
  
  .pre-anim.act .quotes h2 {
    letter-spacing: -0.1vw;
  }

  .pin-wrap .img-hor {
    border-radius: 0;
  }

}

@media screen and (max-width: 500px) {
  .quotes {
    width: 100%;
    left: 0;
    transform: translateY(-8vh);
    padding: 0 10%;}

  .tr--1 {
    width: 100%;
  }
  .tr--1 svg{bottom: -10%;}
  .scroll-down {
    bottom: 9vh;
    left: 50%;
    transform: translate(-50%, 0);
  }
}