@font-face {
  font-family: "FuturaIWS";
  font-weight: normal;
  font-style: normal;
  src: url("./fonts/FuturaIWS.eot");
  src: url("./fonts/FuturaIWS.eot") format("embedded-opentype"), url("./fonts/FuturaIWS.woff2") format("woff2"), url("./fonts/FuturaIWS.woff") format("woff"), url("./fonts/FuturaIWS.ttf") format("truetype"), url("./fonts/FuturaIWS.svg#FuturaIWS") format("svg");
  unicode-range: U+000-5FF;
}

.sc-lock{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 97;
  display: block;
}

.sc-lock.off{
  display: none;
}

.n--se {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url(./components/images/noise.png);
  z-index: 9999999;
  pointer-events: none;
  opacity: .9;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navigation,
.page-nav-a,
button,
.content-pagination a {
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #F6C04B;
  border-radius: 5px;
  width: 0.2vw;
}

::selection {
  background: rgba(246, 192, 75, .9);
  color: #141414;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.sth-scrl {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  top: 0;
  left: 0;
  overflow-x: hidden;
}

body {
  font-family: sans-serif;
  background: #f7f5e9;
  font-size: 0.8vw;
  line-height: 0.8vw;
  position: relative;
  transition: background-color .6s ease;
}
 
